.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  width: 150px;
  margin: 1rem;
  padding: 5px;
  position: relative;
  -webkit-box-shadow: 0 2px 9px 1px #999;
          box-shadow: 0 2px 9px 1px #999;
  background:#6CADDC;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  display: inline-block;
}

.card:hover {
  -webkit-box-shadow: 0 0px 12px 2px #FFEB3B;
          box-shadow: 0 0px 12px 2px #FFEB3B;
  cursor: pointer;

}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.card > .img-container > img {
  height: 80%;
}

.card > .img-cotainer > img:hover {
  height: 90%;
}



